
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
name: "app",
components: {
Tabbar,
TabbarItem,
ViewBox
},
computed: {
isshowtabbar() {
if (
this.$route.path == "/pdd" ||
this.$route.path == "/taobao" ||
this.$route.path == "/jingdong"
) {
return true;
} else {
return false;
}
}
},
methods: {},
async mounted() {



}
};



